import React from 'react';
import { render } from 'react-dom';
import Highcharts from 'highcharts/highstock';
import HighchartsReact from 'highcharts-react-official';

const options = {
  chart: {
    type: 'candlestick'
  },
  title: {
    text: 'A stocks chart'
  },
  rangeSelector: {
    selected: 1
  },
  series: [{
    name: 'AAPL',
    data: [
      [Date.UTC(2022, 0, 1), 140, 150, 200, 220],
      [Date.UTC(2022, 1, 1), 160, 170, 180, 190],
      [Date.UTC(2022, 2, 1), 170, 180, 190, 200],
      [Date.UTC(2022, 2, 5), 120, 130, 150, 125],
      [Date.UTC(2023, 0, 1), 150, 155, 160, 170],
      [Date.UTC(2023, 1, 1), 160, 170, 180, 190],
      [Date.UTC(2023, 2, 1), 170, 180, 190, 200],
      [Date.UTC(2023, 2, 2), 120, 130, 150, 125],
      [Date.UTC(2023, 3, 1), 140, 150, 200, 220],
      [Date.UTC(2023, 4, 1), 160, 170, 180, 190],
      [Date.UTC(2023, 5, 1), 170, 180, 190, 200],
      [Date.UTC(2023, 6, 5), 120, 130, 150, 125],
      [Date.UTC(2023, 7, 1), 150, 155, 160, 170],
      [Date.UTC(2023, 8, 1), 160, 170, 180, 190],
      [Date.UTC(2023, 9, 1), 170, 180, 190, 200],
      [Date.UTC(2023, 10, 2), 120, 130, 150, 125],
      // Add more data points here
    ],
    tooltip: {
      valueDecimals: 2
    }
  }]
};

const Chart2 = () => {
  return (
    <div>
      <HighchartsReact
        highcharts={Highcharts}
        constructorType={'stockChart'} //selecting the type of chart
        options={options}
      />
    </div>
  );
};


export default Chart2;